import React from 'react'
import { Link } from 'react-router-dom';
import styles from "./home.module.css"
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Autoplay } from "swiper";
import "swiper/css";
SwiperCore.use([Autoplay]);

const Home = () => {
  return (
    <div className={styles.container}>
      <div className={styles.card_container}>
        <div className={styles.card_text}>
          <div className={styles.card_main_text}>
             
            <br />
            <span className={styles.text_color}>
              <span className={styles.text_color1}>M</span>
              <span className={styles.text_color2}>B</span>
              <span className={styles.text_color3}>T</span>
              <span className={styles.text_color4}>I</span>
            </span>
            
          </div>
          <div className={styles.card_sub_text}>  ?</div>
        </div>
        <Swiper className={styles.card_image_container} loop={true} autoplay={{delay: 1000}}>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ISTJ.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ISFJ.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/INTP.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/INTJ.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ESFJ.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ESTJ.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ENTJ.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ENTP.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ESTP.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ESFP.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ENFJ.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ENFP.png`)} /><div className={styles.card_image_text}></div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/INFJ.png`)} /><div className={styles.card_image_text}> </div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/INFP.png`)} /><div className={styles.card_image_text}> </div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ISTP.png`)} /><div className={styles.card_image_text}> </div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ISFP.png`)} /><div className={styles.card_image_text}> </div></SwiperSlide>
        </Swiper>
        <div className={styles.card_button}>
          <Link className={styles.card_button_text} to="/survey"> </Link>
        </div>
      </div>
    </div>
  );
}

export default Home;